<template>
    <form id="form">
        <h3 class="text-muted my-5">添加新的Pizza</h3>
        <div class="form-group row">
            <label class="col-sm-1">品种</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model = "newPizza.name">
            </div>
        </div>

        <div class="form-grop row">
            <label class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea rows="5" class="form-control" v-model="newPizza.descroption"></textarea>
            </div>
        </div>
        <p><strong>选项1</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
            <input type="text" class="form-control" v-model="newPizza.size1">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
            <input type="text" class="form-control" v-model="newPizza.price1">
            </div>
        </div>

        <p><strong>选项2</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
            <input type="text" class="form-control" v-model="newPizza.size2">
            </div>
        </div>
            <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
            <input type="text" class="form-control" v-model="newPizza.price2">
            </div>
        </div>
        <div class="form-group row">
            <button v-on:click="addMenuItem" type="button" class="btn btn-success btn-block">添加</button>
        </div>
    </form>
</template>
<script>
    export default{
        data(){
            return{
                newPizza:{}
            }
        },
        methods:{
            addMenuItem(){
                let data = {
                    name:this.newPizza.name,
                    price:this.newPizza.price,
                    options:[
                        {size:this.newPizza.size1,price:this.newPizza.price1},
                        {size:this.newPizza.size2,price:this.newPizza.price2}
                    ]
                };
                 //请求fetch 
                // fetch("https://pizza-app-5582d.firebaseio.com/menu.json",
                //     {
                //         method:"POST",
                //         headers:{
                //             "Content-type":"application/json"
                //         },
                //         body:JSON.stringify(data)
                //     }
                // )
                // .then(
                //     (res) =>res.json
                // )
                // .then(
                //     (data) => this.$router.push({name:'menuLink'})
                // )
                // //请求失败的话执行
                // .catch(
                //     (err) => console.log(err)
                // )

                //axios请求
                // this.http.post("menu.json",data)
                //     .then(
                //         (res) => this.$router.push({name:'menuLink'})
                //     )

                //将数据同步到vuex中
                this.http.post("menu.json",data)
                    .then(
                        () => this.$store.commit("pushToMenuItems",data)
                    )
                    .then(
                        this.newPizza=""
                    )

            }
           

        }
    }
</script>